<template>
  <span class="sts-icon" :class="status | fmtStatusColor" :style="{ marginLeft: point ? '20px' : 0 }">
    <i v-if="point && status !== undefined"></i>{{ status | fmtStatus }}
  </span>
</template>

<script>
const STATUS = {
  0: '结算中',
  1: '部分已结算',
  2: '已结算'
}

export default {
  name: 'stmt-sts-icon',
  props: {
    status: [Number, String],
    point: Boolean
  },
  filters: {
    // 状态文案
    fmtStatus (val) {
      return val === undefined ? '' : (STATUS[val] ? STATUS[val] : '未知')
    },

    // 状态颜色
    fmtStatusColor (val, type = 'color') {
      switch (STATUS[val]) {
        case '结算中':
        case '部分已结算': return 'color-orange'
        case '已结算': return 'color-green'
        default: return 'color-black'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import './stsIcon.scss';
</style>
