<template>
  <div class="chart-container">
    <h5 class="chart-title">
      积分发行活动统计
      <el-popover content="统计本结算期内，本部门发行的积分在各活动中回收的组成情况">
        <el-link :underline="false" icon="el-icon-question" slot="reference"></el-link>
      </el-popover>
    </h5>

    <div class="chart-main">
      <div class="chart">
        <div class="total"><span>{{ total }}</span>总计</div>
        <v-chart :options="options"></v-chart>
      </div>
      <div class="table">
        <el-table
          :data="datas"
          size="small"
          show-summary
          :summary-method="getSummaries"
          style="width: 100%">
          <el-table-column prop="name" label="">
            <template slot-scope="scope">
              <span class="colorTag"><i></i><el-link type="primary" @click="popupActIntro(scope.row.aid)">{{ scope.row.name }}</el-link></span>
            </template>
          </el-table-column>
          <el-table-column prop="integral" label="积分">
            <template slot-scope="scope">{{ scope.row.integral | fmtMoney }}</template>
          </el-table-column>
          <el-table-column label="占比">
            <template slot-scope="scope">{{ (scope.row.integral / total) | fmtFloat2Percent }}</template>
          </el-table-column>
          <el-table-column prop="lastIntegral" label="去年同期">
            <template slot-scope="scope">{{ scope.row.lastIntegral | fmtMoney }}</template>
          </el-table-column>
          <el-table-column prop="lastProportion" label="去年同期占比">
            <template slot-scope="scope">{{ scope.row.lastProportion | fmtFloat2Percent }}</template>
          </el-table-column>
          <el-table-column label="同比增长">
            <template slot-scope="scope">
              <span>{{ (Math.abs((scope.row.integral - scope.row.lastIntegral)) / scope.row.lastIntegral) | fmtFloat2Percent }}<i :class="'el-icon-' + ((scope.row.integral - scope.row.lastIntegral) > 0 ? 'top-right' : 'bottom-right')" :style="{ color: ((scope.row.integral - scope.row.lastIntegral) > 0 ? '#19be6b' : '#ed4014') }"></i></span>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>

    <popup-act-intro :visible.sync="actIntroVisible" :aid="selectedAId"></popup-act-intro>
  </div>
</template>

<script>
import pie from './common/pie'
import PopupActIntro from '@/views/popups/PopupActIntro'

export default {
  name: 'chart-department-use-activity',
  mixins: [pie],
  components: {
    PopupActIntro
  },
  data () {
    return {
      options: {
        tooltip: {
          formatter: '{b}<br/>积分：{c}<br/>积分占比：{d}%'
        },
        series: [
          {
            name: '积分发行活动统计',
            type: 'pie',
            radius: ['60%', '90%'],
            label: { show: false },
            labelLine: { show: false },
            data: []
          }
        ]
      },
      actIntroVisible: false,
      selectedAId: null
    }
  },
  methods: {
    isMatchCondition (column) {
      return column.property === 'integral' || column.property === 'lastIntegral'
    },

    popupActIntro (aid) {
      this.selectedAId = aid
      this.actIntroVisible = true
    }
  }
}
</script>

