<template>
  <div class="settlement-detail" v-loading="isLoading">
    <el-row :gutter="gutter">
      <el-col>
        <el-new-card>
          <div style="font-size: 14px; font-weight: bold;">
            {{ $route.meta.name }}<stmt-sts-icon :status="info.status" point />
          </div>
        </el-new-card>
      </el-col>
    </el-row>

    <el-row :gutter="gutter">
      <!-- 结算信息 -->
      <el-col :span="12">
        <el-new-card>
          <template slot="header">结算信息</template>

          <info-block style="height: 200px; margin-bottom: 0;">
            <info-item label="结算期数：">{{ info.stmtName }}</info-item>
            <info-item label="结算类型：">{{ info.stmtType }}</info-item>
            <info-item label="结算周期：">{{ info.startTime | fmtDate }} 至 {{ info.endTime | fmtDate }}</info-item>
            <info-item label="结算生成时间：">{{ info.createTime | fmtDateTime }}</info-item>
            <info-item label="本期回收积分："><strong style="font-weight: normal;font-size: 18px;">{{ info.recycle | fmtMoney }}</strong></info-item>
          </info-block>
        </el-new-card>
      </el-col>

      <!-- 本期回收积分组成概览 -->
      <el-col :span="12">
        <el-new-card>
          <template slot="header">
            <span>
              本期结算积分组成总览
              <el-popover content="统计本结算期内，所有部门发行的积分被回收的比例分布情况">
                <el-link :underline="false" icon="el-icon-question" slot="reference"></el-link>
              </el-popover>
            </span>
          </template>

          <chart-recycle-overview :datas="overView"></chart-recycle-overview>
        </el-new-card>
      </el-col>
    </el-row>

    <!-- 本期账单 -->
    <el-row :gutter="gutter">
      <el-col>
        <el-new-card>
          <template slot="header">
            本期账单
            <el-button size="small" icon="el-icon-tickets" @click="popupLastDetail">查看上期异动核减详情</el-button>
          </template>

          <el-table
            :data="billList"
            size="small"
            stripe
            style="width: 100%">
            <el-table-column label="序号" align="center" width="80px">
              <template slot-scope="scope">
                <span>{{ scope.$index + 1 }}</span>
              </template>
            </el-table-column>
            <el-table-column prop="payer" label="付款方" align="center"></el-table-column>
            <el-table-column prop="payee" label="收款方" align="center"></el-table-column>
            <el-table-column prop="money" label="金额（元）" align="center"></el-table-column>
            <el-table-column prop="status" label="确认状态" align="center">
              <template slot-scope="scope">
                <span :style="scope.row.status | fmtBillStatusColor">{{ scope.row.status | fmtBillStatus }}</span>
              </template>
            </el-table-column>
            <el-table-column prop="stmtMan" label="收款确认人" align="center"></el-table-column>
            <el-table-column prop="stmtDate" label="收款确认时间" align="center">
              <template slot-scope="scope">
                <span>{{ scope.row.stmtDate | fmtDateTime }}</span>
              </template>
            </el-table-column>
            <el-table-column label="操作" align="center">
              <template slot-scope="scope">
                <el-popconfirm
                  v-if="scope.row.status === 1"
                  title="是否确认收款？"
                  icon="el-icon-warning"
                  iconColor="#F56C6C"
                  confirmButtonType="danger"
                  cancelButtonText="否"
                  confirmButtonText="是"
                  @onConfirm="onReceipt(scope.row.id)">
                  <el-button slot="reference" type="text" size="small">确认收款</el-button>
                </el-popconfirm>
                <span v-else>-</span>
              </template>
            </el-table-column>
          </el-table>
        </el-new-card>
      </el-col>
    </el-row>

    <!-- 各部门统计数据 -->
    <el-tabs type="border-card">
      <el-tab-pane :label="item.name" v-for="(item, index) in deptDatas" :key="'deptDatas' + index">
        <h5 class="sub-title">应付</h5>

        <el-row :gutter="gutter">
          <el-col :span="12">
            <chart-dept-use-for :datas="item.deptUseFor"></chart-dept-use-for>
          </el-col>
          <el-col :span="12">
            <chart-dept-used-fee :datas="item.deptUsedFee"></chart-dept-used-fee>
          </el-col>
        </el-row>
        <el-divider></el-divider>
        <el-row :gutter="gutter">
          <el-col :span="24">
            <chart-dept-use-act :datas="item.deptUseAct"></chart-dept-use-act>
          </el-col>
        </el-row>
        <el-row :gutter="gutter">
          <el-col :span="24">
            <chart-dept-use-goods :datas="item.deptUseGoods"></chart-dept-use-goods>
          </el-col>
        </el-row>

        <el-divider></el-divider>

        <h5 class="sub-title">应收</h5>

        <el-row :gutter="gutter">
          <el-col :span="12">
            <chart-dept-recyle-from :datas="item.deptRecyleFrom"></chart-dept-recyle-from>
          </el-col>
          <el-col :span="12">
            <chart-dept-goods-fee :datas="item.deptGoodsFee"></chart-dept-goods-fee>
          </el-col>
        </el-row>
        <el-divider></el-divider>
        <el-row :gutter="gutter">
          <el-col :span="24">
            <chart-dept-recyle-act :datas="item.deptRecyleAct"></chart-dept-recyle-act>
          </el-col>
        </el-row>
        <el-row :gutter="gutter">
          <el-col :span="24">
            <chart-dept-changed-goods :datas="item.deptChangedGoods"></chart-dept-changed-goods>
          </el-col>
        </el-row>
      </el-tab-pane>
    </el-tabs>

    <last-detail :visible.sync="lastDetailVisiable" @popupAct="popupActIntro" @popupCredits="popupCredits"></last-detail>
    <popup-act-intro :visible.sync="actIntroVisible" :aid="selectedAId"></popup-act-intro>
    <popup-credits :visible.sync="creditsVisible" :rid="selectedRId" @popupAct="popupActIntro"></popup-credits>
  </div>
</template>

<script>
import ElNewCard from '@/components/ElNewCard'
import InfoBlock from '@/components/InfoBlock'
import InfoItem from '@/components/InfoItem'
import recycleOverview from '@/components/charts/recycleOverview'
import stmtStsIcon from '@/components/status/StmtStsIcon'
import deptUseFor from '@/components/charts/deptUseFor'
import deptUsedFee from '@/components/charts/deptUsedFee'
import deptUseAct from '@/components/charts/deptUseAct'
import deptUseGoods from '@/components/charts/deptUseGoods'
import deptRecyleFrom from '@/components/charts/deptRecyleFrom'
import deptGoodsFee from '@/components/charts/deptGoodsFee'
import deptRecyleAct from '@/components/charts/deptRecyleAct'
import deptChangedGoods from '@/components/charts/deptChangedGoods'
import LastDetail from './LastDetail'
import PopupActIntro from '@/views/popups/PopupActIntro'
import PopupCredits from '@/views/popups/PopupCredits'

export default {
  name: 'settlement-detail',
  components: {
    ElNewCard,
    InfoBlock,
    InfoItem,
    stmtStsIcon,
    ChartRecycleOverview: recycleOverview,
    ChartDeptUseFor: deptUseFor,
    ChartDeptUsedFee: deptUsedFee,
    ChartDeptUseAct: deptUseAct,
    ChartDeptUseGoods: deptUseGoods,
    ChartDeptRecyleFrom: deptRecyleFrom,
    ChartDeptGoodsFee: deptGoodsFee,
    ChartDeptRecyleAct: deptRecyleAct,
    ChartDeptChangedGoods: deptChangedGoods,
    LastDetail,
    PopupActIntro,
    PopupCredits
  },
  data () {
    return {
      gutter: 30,
      info: {
        status: '',
        stmtName: '',
        stmtType: '定期',
        startTime: '',
        endTime: '',
        createTime: '',
        recycle: ''
      },
      billList: [],
      overView: [],
      deptDatas: [],
      isLoading: false,
      lastDetailVisiable: false,
      actIntroVisible: false, // 活动简介弹窗控制
      selectedAId: 1, // 弹窗的活动的id
      creditsVisible: false, // 积分组成详情弹窗控制
      selectedRId: 1 // 弹窗的积分组成详情的id
    }
  },
  created () {
    this.getData(this.$route.query.id)
  },
  methods: {
    getData (id) {
      this.isLoading = true
      this.$http.serviceGetStmtDetail({ id })
        .then(res => {
          this.info = res.info
          this.billList = res.list
          this.overView = res.overview
          this.deptDatas = res.deptDatas
        })
        .finally(() => {
          this.isLoading = false
        })
    },

    // 确认收款
    onReceipt (id) {
      this.getData(this.$route.query.id)
    },

    // 弹窗上期异动核减详情
    popupLastDetail () {
      this.lastDetailVisiable = true
    },

    popupActIntro (aid) {
      this.selectedAId = aid
      this.actIntroVisible = true
    },

    popupCredits (rid) {
      this.selectedRId = rid
      this.creditsVisible = true
    }
  }
}
</script>

<style lang="scss" scoped>
.settlement-detail {
  .stmt-sts {
    margin-left: 20px;
    font-size: 12px;
    font-weight: normal;
    color: #606266;
    i {
      display: inline-block;
      margin-right: 3px;
      width: 9px;
      height: 9px;
      border-radius: 50%;
      background-color: #909399;
    }
  }

  .info-item {
    width: 500px;
  }

  .el-row {
    margin-bottom: 30px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  .sub-title {
    margin-top: 4px;
    margin-bottom: 20px;
    font-size: 13px;

    &:not(:first-child) {
      margin-top: 24px;
    }
  }

  .el-tabs--border-card {
    border-color: #EBEEF5;
    box-shadow: none;
    border-radius: 4px;
    overflow: hidden;
  }
}
</style>
