<template>
  <el-dialog
    class="last-detail"
    title="上期异动核减详情"
    width="80%"
    :visible.sync="show"
    destroy-on-close
    v-loading="loading"
    @closed="onClose"
  >
    <h5 class="sub-title">上期异动核减对账单</h5>
    <el-table
      :data="billList"
      size="small"
      stripe
      style="width: 100%">
      <el-table-column label="序号" align="center">
        <template slot-scope="scope">
          {{ scope.$index }}
        </template>
      </el-table-column>
      <el-table-column prop="payer" label="付款方" align="center"></el-table-column>
      <el-table-column prop="payee" label="收款方" align="center"></el-table-column>
      <el-table-column prop="points" label="积分" align="center">
        <template slot-scope="scope">
          {{ scope.row.points | fmtMoney }}
        </template>
      </el-table-column>
      <el-table-column prop="momey" label="金额（元）" align="center">
        <template slot-scope="scope">
          {{ scope.row.momey | fmtMoney }}
        </template>
      </el-table-column>
    </el-table>

    <el-divider></el-divider>

    <h5 class="sub-title">上期异动核减明细</h5>
    <el-new-table :apiName="apiName" size="small" type="popup">
      <el-table-column prop="credits" label="积分数值" align="center" min-width="100">
        <template slot-scope="scope">
          <span :style="scope.row.credits | creditStatusColor">{{ scope.row.credits | creditStatus }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="model" label="方式" align="center"></el-table-column>
      <el-table-column prop="type" label="类型" align="center"></el-table-column>
      <el-table-column prop="user" label="用户名称" align="center" min-width="120"></el-table-column>
      <el-table-column prop="plat" label="活动/核销平台" align="center" min-width="120">
        <template slot-scope="scope">
          <el-link type="primary" v-if="scope.row.plat.includes('活动')" @click="popupActIntro(scope.row.aid)">{{ scope.row.plat }}</el-link>
          <span v-else>{{ scope.row.plat || '-' }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="behavior" label="履历名称" align="center" min-width="120"></el-table-column>
      <el-table-column prop="commodity" label="商品名称" align="center" min-width="120">
        <template slot-scope="scope">
          <span>{{ scope.row.commodity || '-' }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="account" label="积分组成" align="center" min-width="120">
        <template slot-scope="scope">
          <el-link type="primary" @click="popupCredits(scope.row.id)">查看详情</el-link>
        </template>
      </el-table-column>
      <el-table-column prop="date" label="时间" align="center" min-width="150">
        <template slot-scope="scope">
          <span>{{ scope.row.date | fmtDateTime }}</span>
        </template>
      </el-table-column>
    </el-new-table>

    <el-divider></el-divider>

    <div class="dialog-footer">
      <el-button size="small" plain @click="show = false">关 闭</el-button>
    </div>
  </el-dialog>
</template>

<script>
import ElNewTable from '@/components/ElNewTable'

export default {
  name: 'popup-activity-intro',
  model: {
    prop: 'visible',
    event: 'change'
  },
  components: {
    ElNewTable
  },
  props: {
    visible: Boolean
  },
  data () {
    return {
      apiName: 'serviceGetLastDetail',
      show: false,
      loading: false,
      billList: [
        {
          payer: 'ABC',
          payee: 'DEF',
          points: 123456,
          momey: 100
        }
      ]
    }
  },
  watch: {
    visible (val) {
      this.show = val

      val && this.getData()
    }
  },
  filters: {
    creditStatus (val) {
      const v = Number(val)
      if (isNaN(v) || v === 0) return val

      return (v > 0 ? '+' : '') + v
    },

    creditStatusColor (val) {
      const v = Number(val)
      if (isNaN(v) || v === 0) return ''

      return { color: v > 0 ? '#19be6b' : '#ed4014' }
    }
  },
  methods: {
    getData () {

    },

    onClose () {
      this.$emit('update:visible', this.show)
    },

    toActDetail (id) {
      this.$router.push({
        path: '/activityMgmt/actDetail',
        query: {
          id
        }
      })
    },

    popupActIntro (id) {
      this.$emit('popupAct', id)
    },

    popupCredits (id) {
      this.$emit('popupCredits', id)
    }
  }
}
</script>

<style lang="scss" scoped>
.last-detail {
  .sub-title {
    margin-top: 4px;
    margin-bottom: 20px;
    font-size: 13px;

    &:not(:first-child) {
      margin-top: 24px;
    }
  }

  .dialog-footer {
    text-align: center;

    .el-button {
      min-width: 100px;
    }
  }
}
</style>
